.img-transparent {
  opacity: 0.75;
  background-color: white;
}

.text-block {
  opacity: 0.7;
  position: absolute;
  text-align: center;
  background-color: black;
  color: white;
}

.container * {
  margin: auto;
  /* width: 35vw; */
}


@media only screen and (max-width: 600px) {
  .container h2, .container h4 {
    text-align: center;
    width: auto;
  }

  .container * {
    /* width: 60vw; */
  }
}

h6 {
  font-size: large;
}

img {
  max-width: 100%;
}

.project-row {
  padding: 2%;
  border-top: 1px solid grey;
}

body {
  margin: 0;
  padding: 0;
}

#root {
  min-height: 100vh;
  background-color: #ebf9ff;
}

.h-80 {
  min-height: 80vh;
}

.bg-blue {
  background-color: #3c9fc9;
}

.nav-link {
  color: white;
}

.nav-link:hover {
  color: grey;
}

a {
  color: blue;
}

a:hover {
  color: #4d94ff;
}

li {
  list-style-type: none;
  display: inline;
}

